#index, #index > div {
	width: 100%;
	height: 100%;
}
.change {
	position: relative;
	bottom: 30px;
	margin: 5px;
	float: right;
}

#tetris {
	margin: 10px;

	display: inline-block;

	background-color: #ddd;
}
#tetris h2 {
	margin: 0;
	padding: 10px;

	background-color: rgba(255, 255, 255, 0.3);
}
#tetris > div {
	margin: 10px;

	display: inline-block;
	float: left;
}
#tetris .field {
	background-color: #333;
}
#tetris .t-panel {
	width: 150px;

	background-color: rgba(255, 255, 255, 0.3);
}
#tetris .t-panel .panel-row {
	width: 90%;
	margin: 5%;

	display: inline-block;

	background-color: #ddd;
}
#tetris .t-panel .panel-row .panel-title {
	margin: 10px;

	text-align: center;

	font-weight: bold;
}
#tetris .t-panel .panel-row .br {
	width: 100%;
	height: 1px;

	display: block;

	background-color: #fff;
}
#tetris .t-panel .panel-row .panel-text {
	margin: 10px;

	text-align: center;

	font-size: large;
}
#tetris .t-panel .panel-row .control-btn {
	width: 90%;
	margin: 5%;
	padding: 3%;
}
#tetris .t-panel .next .next-block {
	width: 100%;
	height: 100px;

	display: inline-block;

	background-color: #333;
}

#intro-comp {
	wi dth: 100%;
	hei ght: 100%;
	background-color: #ddd;
}
.title {
	position: relative;
	margin: 0;
}























































































#info #profile .panel-body > div {
	display: inline-block;
	margin: 10px;
}
#info #profile .panel-body #my-photo {
	width: 130px;
}
#info #profile .panel-body #my-info {
	vertical-align: top;
}
#info #profile .panel-body #my-info div label {
	width: 70px;
}
#info #profile .panel-body #my-info div span {
	display: inline-block;
}
#info #history .panel-body > div {
	margin: 10px;
}
#info #history .panel-body div label {
	width: 160px;
}
#info #history .panel-body div span {
	display: inline-block;
	vertical-align: top;
}
@media (max-width: 480px) {
#info #profile .panel-body #my-photo {
		width: 100%;
}
#info #profile .panel-body #my-info {
		width: 100%;
}
#info #profile .panel-body #my-info div label {
		width: 100%;
		margin-bottom: 0;
}
#info #profile .panel-body #my-info div span {
		margin-left: 20px;
}
#info #profile .panel-body #my-info div span .form-control-static {
		padding-top: 0;
}
#info #history .panel-body div label {
		width: 100%;
		margin-bottom: 0;
}
#info #history .panel-body div span {
		margin-left: 20px;
}
}


































































